    }
    
    
    .contact_title{
        text-align: center;
        margin: 100px;
        font-size: 40px;
    }
      .contact_flex{
          display: flex;
          /* align-items: center; */
          justify-content: center;
          max-width: 1366px;
          padding: 20px;
          margin: auto;
          line-height: 1.8em;
          
          
      }
      .contact_left{
          width: 100%;
      }
      .contact_right{
      width: 100%;
    }
    
    
    
    
    
    /* .contact .container--flex{
      align-items: flex-start;
    } */
    
    
    .contact-information{
      padding:10px;
      padding-top: 0;
      margin-bottom: 30px;
      border-bottom: 1px solid #DE423A;
    }
    
    .contact-information .column__title{
      font-family: 'Pacifico', cursive;
      font-weight: 100;
    }
    
    
    .contact-information [class*="icon-"]{
      font-size: 1.3em; 
      margin-right: 7px;
      position: relative;
      top: 5px;
    }
    
    .contact-information .social-icon{
      justify-content: flex-start;
      margin-bottom: 10px;
    }
    
    .contact-information .social-icon [class*="icon-"]{
      background:black;
     
      margin-left: 0;
      margin-right: 15px;
      
    }
    
    .formulario{
      /* padding: 10px; */
      
      
    }
    
    .formulario__label, .formulario__input-txt, .formulario__textarea{
      display:block;
      width:100%;
    }
    
    .formulario__input-txt{
      padding:10px;
      margin-bottom:10px;
    }
    
    .formulario__textarea{
      min-height: 100px;
      min-width: 100%;
      max-width: 100%;
      max-height: 200px;
    }
    
    .formulario__btn{
      border:none;
      cursor: pointer;
      font-size: 1em;
    }
    
    
    @media(max-width:768px){
         
        .contact_left{
            margin-bottom: 100px;
        }
    
    
    
    
        .contact_flex{
      flex-direction: column;
            
            
        }
    }